import React from 'react';
import { motion } from 'framer-motion';
import { useScrollAnimation } from '../hooks/useScrollAnimation';
import SEO from '../components/SEO';
import { staggerContainer, staggerItem } from '../utils/animations';
import GlassmorphicCard from '../components/common/GlassmorphicCard';
import GradientText from '../components/common/GradientText';
import styles from './Contact.module.css';

const Contact: React.FC = () => {
  useScrollAnimation();

  const structuredData = {
    "@context": "https://schema.org",
    "@type": "ContactPage",
    "name": "Contact Joey Audi",
    "description": "Get in touch with Joey Audi for audiovisual production, event coordination, and video content creation opportunities.",
    "url": "https://joeyaudi.com/contact",
    "mainEntity": {
      "@type": "Person",
      "name": "Joey Audi",
      "email": "contact@joeyaudi.com",
      "address": {
        "@type": "PostalAddress",
        "addressLocality": "Tampere",
        "addressCountry": "Finland"
      },
      "sameAs": [
        "https://www.instagram.com/joeyaudi2/",
        "https://www.linkedin.com/in/joey-audi"
      ]
    }
  };

  return (
    <div className={styles.page}>
      <SEO 
        title="Contact"
        description="Get in touch with Joey Audi for audiovisual production, event coordination, and video content creation opportunities. Based in Tampere, Finland."
        structuredData={structuredData}
      />
      <div className={styles.gradientBg}></div>
      <div className="container">
        <GradientText as="h1" gradient="primary" className="section-title fade-in">
          Let's Connect
        </GradientText>
        
        <motion.div 
          className={styles.contactWrapper}
          variants={staggerContainer}
          initial="initial"
          animate="animate"
        >
          <GlassmorphicCard 
            className={styles.contactIntro}
            hover={false}
            initial={{ opacity: 0, y: 20 }}
            whileInView={{ opacity: 1, y: 0 }}
            viewport={{ once: true }}
            transition={{ duration: 0.6 }}
          >
            <p>
              I'm currently seeking full-time opportunities in audiovisual production, event coordination, 
              and video content creation. Based in Tampere, Finland (EET timezone), I'm interested in roles 
              where I can apply my experience in live events, technical operations, and multimedia production. 
              Please reach out via email at contact@joeyaudi.com, or connect with me on LinkedIn.
            </p>
            <div className={styles.availabilityBadge}>
              <span className={styles.pulseDot}></span>
              Available for full-time opportunities
            </div>
          </GlassmorphicCard>

          <motion.div className={styles.contactMethodsGrid} variants={staggerItem}>
            <a href="mailto:contact@joeyaudi.com" style={{ textDecoration: 'none' }}>
              <GlassmorphicCard
                className={styles.contactCard}
                gradient="primary"
                whileHover={{ scale: 1.05, y: -8 }}
                whileTap={{ scale: 0.98 }}
                transition={{ duration: 0.2 }}
              >
                <div className={styles.cardIcon}>
                  <i className="fas fa-envelope"></i>
                </div>
                <h3>Email</h3>
                <p>contact@joeyaudi.com</p>
                <span className={styles.cardAction}>Send an email →</span>
              </GlassmorphicCard>
            </a>

            <a href="https://www.linkedin.com/in/joeyaudi" target="_blank" rel="noopener noreferrer" style={{ textDecoration: 'none' }}>
              <GlassmorphicCard
                className={styles.contactCard}
                gradient="secondary"
                whileHover={{ scale: 1.05, y: -8 }}
                whileTap={{ scale: 0.98 }}
                transition={{ duration: 0.2 }}
              >
                <div className={styles.cardIcon}>
                  <i className="fab fa-linkedin"></i>
                </div>
                <h3>LinkedIn</h3>
                <p>Connect professionally</p>
                <span className={styles.cardAction}>View profile →</span>
              </GlassmorphicCard>
            </a>

            <a href="https://www.instagram.com/joeyaudi2/" target="_blank" rel="noopener noreferrer" style={{ textDecoration: 'none' }}>
              <GlassmorphicCard
                className={styles.contactCard}
                gradient="accent"
                whileHover={{ scale: 1.05, y: -8 }}
                whileTap={{ scale: 0.98 }}
                transition={{ duration: 0.2 }}
              >
                <div className={styles.cardIcon}>
                  <i className="fab fa-instagram"></i>
                </div>
                <h3>Instagram</h3>
                <p>Follow my journey</p>
                <span className={styles.cardAction}>View profile →</span>
              </GlassmorphicCard>
            </a>

            <a href="https://discord.com/users/joeyaudi" target="_blank" rel="noopener noreferrer" style={{ textDecoration: 'none' }}>
              <GlassmorphicCard
                className={styles.contactCard}
                gradient="accent"
                whileHover={{ scale: 1.05, y: -8 }}
                whileTap={{ scale: 0.98 }}
                transition={{ duration: 0.2 }}
              >
                <div className={styles.cardIcon}>
                  <i className="fab fa-discord"></i>
                </div>
                <h3>Discord</h3>
                <p>joeyaudi</p>
                <span className={styles.cardAction}>Add me →</span>
              </GlassmorphicCard>
            </a>
          </motion.div>
        </motion.div>
      </div>
    </div>
  );
};

export default Contact;
